<template>
  <div>
    <da-equal-panel height="auto">
      <div slot="left">
        <da-radio-block v-model="rdbSubitem" :data-lists="subitemList" label="subitemName" value-key="id" @change="getSubitemTreeData"></da-radio-block>
        <da-panel overflow="auto" padding="t" :noHead="true" height="467px">
          <da-tree v-model="submitTreeSelectData" :data-lists="subitemTreeData" :default-expand="true" @currentChange="subitemTreeChange" :show-checkbox="true" :check-strictly="true" :defaultKeys="treeDefaultKeys"></da-tree>
        </da-panel>
      </div>
      <div class="clearfix" slot="right">
        <da-panel :noHead="true" padding="l" padding-content="lrtb">
          <da-time-select v-model="selectTime" :time-type.sync="dateType" :show-time="[8,5]" @change="changeTime"></da-time-select>
          <button type="button" class="el-button el-button--primary" @click="loadData">
            <span>查询</span>
          </button>
        </da-panel>
        <div class="coany-energy-block pad-10">
          <carbon-data :data-lists="carbonData" width="25%">
            <div slot-scope="scope">
              <h4 class="text-left">{{scope.data.name}}</h4>
              <p class="cost-any-value">{{scope.data.value}}</p>
              <p>{{scope.data.unit}}</p>
              <p class="text-center">同比：{{scope.data.tbScale}}%

                <img :src="scope.data.tbRatio==-1?'':(scope.data.tbRatio>0?up:down)">
              </p>
              <p class="text-center">环比：{{scope.data.hbScale}}%

                <img :src="scope.data.hbRatio==-1?'':(scope.data.hbRatio>0?up:down)">
              </p>
            </div>
          </carbon-data>
        </div>
        <da-panel title="总成本分析" width="100%" height="auto" min-height="200px" padding="l" ref="panelCount">
          <da-line-chart id="costAnalysis" type="line" x-axis-key="dataTime" value-key="dataValue" :data-lists="costAnalysisData" :date-time="selectTime" :format="dateFormat" :x-axis-format="xAxisFormat" label="contentName"></da-line-chart>

        </da-panel>
      </div>
    </da-equal-panel>

    <da-panel title="能源成本趋势分析" width="50%" height="auto" min-height="200px" padding="rt" ref="panelCount">
      <da-line-chart id="egyCostTendency" type="bar" x-axis-key="dataTime" value-key="dataValue" :data-lists="egyCostTdcyData" :date-time="selectTime" :format="dateFormat" :x-axis-format="xAxisFormat" label="contentName"></da-line-chart>

    </da-panel>

    <da-panel title="能源成本占比分析" width="50%" height="auto" min-height="200px" padding="t" ref="panelCount">
      <da-pie-chart id="egyCostProportion" title="" :data-lists="scaleData" label="name" valueKey="value"></da-pie-chart>
    </da-panel>
  </div>
</template>

<script>
import carbonData from "../analysis/components/carbonData";
import carbonCount from "../analysis/components/carbonCount";

import up from "@/assets/images/up.png";
import down from "@/assets/images/down.png";
import {
  deepJSONCopy,
  getListByTree,
  changeTree,
  reduceById,
  calcTime,
  cycleTime,
  getFixedDisplayData
} from "../../../util/util";
export default {
  components: {
    carbonData,
    carbonCount
  },
  data() {
    return {
      up,
      down,
      carbonData: [],

      rdbSubitem: "", //组织架构or设备
      subitemList: [
        { id: 1, subitemName: "组织架构" },
        { id: 2, subitemName: "设备" }
      ],
      dateType: 8, //日期类型
      selectTime: "", //选择的时间
      submitTreeSelectData: "", //树形选择的对象
      subitemTreeData: [], //左侧树形数据
      treeDefaultKeys: {},
      energyTypeData: [], //能源种类列表
      startTime: new Date(), //开始时间
      endTime: new Date(), //结束时间
      bq_costData: [], //本期能源成本数据
      tb_costData: [], //同比能源成本数据
      hb_costData: [], //环比能源成本数据
      orgTreeDisplace: {},
      energyDisplace: {},
      costAnalysisData: [], //总成本分析数据
      costAnalysisData_temp: [], //总成本分析数据
      egyCostTdcyData: [], //能源成本趋势分析数据

      bqEgyCostPptData: [], //能源成本占比分析数据(本期)
      tbEgyCostPptData: [], //能源成本占比分析数据（同比）
      hbEgyCostPptData: [], //能源成本占比分析数据（环比）
      scaleData: [], //占比分析数据
      bgColor: [], //色块样式配置
      energyIds: "", //能源种类ID
      dateFormat: "YYYY-MM", //
      xAxisFormat: "DD日", //X轴日期格式化
      legendFormat: "YYYY年MM月"
    };
  },
  methods: {
    //初始化数据
    inintData() {
      this.dateType = 8;
      let t1 = new Date();
      t1 = this.$moment(t1, "YYYY");
      this.selectTime = calcTime(t1, -1, 8, "YYYY"); //设置为去年
      this.bgColor = ["#548630", "#54ddde", "#456dca", "#a578d3"];
      this.getSubitemTreeData();
    },
    //加载页面数据
    loadData() {
      this.bq_costData = [];
      this.tb_costData = [];
      this.hb_costData = [];
      this.costAnalysisData = [];
      this.costAnalysisData_temp = [];
      this.egyCostTdcyData = [];
      this.bqEgyCostPptData = [];
      this.tbEgyCostPptData = [];
      this.hbEgyCostPptData = [];
      this.carbonData = [];
      this.scaleData = [];
      this.getStartEndTime("本期");
      let params = this.getQueryDataParams(); //根据选择的条件生成参数
      this.getCostAnalysisData(params, "本期");
    },
    //获取能源类型数据
    getEnergyTypeData() {
      let me = this;
      let params = {
        title: "能源种类",
        orgIds: "",
        equipGroups: "",
        equipIds: ""
      };
      if (me.rdbSubitem == 1) {
        //组织架构
        params.orgIds = me.submitTreeSelectData;
        params.equipGroups = "";
        params.equipIds = "";
      } else if (me.rdbSubitem == 2) {
        //设备
        params.orgIds = "";
        params.equipGroups = "";
        params.equipIds = me.submitTreeSelectData;
      }
      me
        .$ajax({
          url: "rest/analysisData/energy",
          method: "get",
          params: params
        })
        .then(res => {
          if (res.data.status) {
            me.energyTypeData = res.data.records;

            for (let item in me.energyTypeData) {
              me.energyIds += me.energyTypeData[item].id + ",";
            }
            me.energyIds = me.energyIds.substring(0, me.energyIds.length - 1);

            let toObj = {};
            changeTree(res.data.records, "id", "name", toObj);
            me.energyDisplace = toObj;

            me.loadData();
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },

    //日期改变事件
    changeTime(date, type) {
      if (date) {
        this.dateType = type;
        this.selectTime = date;
      }
    },
    //点击树事件
    subitemTreeChange(node) {
      this.submitTreeSelectData = node.obj.id;
      this.loadData();
    },
    //获取树列表数据
    getSubitemTreeData() {
      let me = this;
      let treeUrl = ["departTree", "equip"];
      let paramsObj = { title: "树形条件" };

      me
        .$ajax({
          url: "rest/analysisData/" + treeUrl[this.rdbSubitem - 1],
          method: "get",
          params: paramsObj
        })
        .then(res => {
          if (res.data.status) {
            this.treeDefaultKeys = {
              nodeKey: "id",
              checkedKeys: [res.data.records[0].id],
              expandedKeys: []
            };
            this.subitemTreeData = res.data.records;
            this.submitTreeSelectData = res.data.records[0].id.toString();
            let toObj = {};
            changeTree(res.data.records, "id", "name", toObj);
            this.orgTreeDisplace = toObj;

            this.getEnergyTypeData();
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    /**
     * 根据选择的条件生成参数
     */
    getQueryDataParams() {
      let me = this;
      let type = me.rdbSubitem;
      // let typeStr = { month: 5, year: 8 };
      // let cycle = typeStr[me.dateType];
      let cycle = "";
      if (me.dateType == 5) {
        cycle = 3;
        me.dateFormat = "YYYY-MM";
        me.xAxisFormat = "DD日";
        me.legendFormat = "YYYY年MM月";
      } else if (me.dateType == 8) {
        cycle = 5;
        me.dateFormat = "YYYY";
        me.xAxisFormat = "MM月";
        me.legendFormat = "YYYY年";
      }

      let params = {
        title: "",
        orgIds: me.submitTreeSelectData, //组织架构ID
        targetCycle: cycle, //指标周期
        targetTypes: me.$store.state.targetConstant.targetType.energyCost, //指标类型
        // contentTypes: me.$store.state.targetConstant.targetContentType.energy, //统计内容类型
        // contentIds: me.energyIds, //统计内容ID
        contentTypes: "", //统计内容类型
        contentIds: "", //统计内容ID
        objTypes: "", //指标关联对象类型
        objTypeValues: "", //指标关联对象类型对应的值
        isQueryAll: false, // Boolean,是否所有指标数据
        // benchmarkQuota: "000", //表示查询的是标杆数据，[标杆][定额][目标]
        startTime: this.startTime, //开始时间
        endTime: this.endTime, //结束时间
        isOrgId: false, // Boolean,是否返回部门ID
        isContentType: true, // Boolean,是否返回指标统计内容类型ID
        isObjType: "" // String,是否返回指标关联对象,1为班组，2为设备，3为产品
      };
      if (type == 1) {
        //组织架构
        params.orgIds = me.submitTreeSelectData;
        params.objTypes = "";
        params.objTypeValues = "";
      } else if (type == 2) {
        //设备
        params.orgIds = "";
        params.objTypes =
          me.$store.state.targetConstant.targetSubordinate.equip;
        params.objTypeValues = me.submitTreeSelectData;
      }

      return params;
    },
    //获得开始日期和结束日期
    //type:本期、同比、环比
    getStartEndTime(type) {
      let datatime = "";

      if (type == "本期") {
        if (this.dateType == 5) {
          datatime = this.selectTime;
        } else if (this.dateType == 8) {
          datatime = this.selectTime;
        }
      } else if (type == "同比") {
        if (this.dateType == 5) {
          datatime = calcTime(this.selectTime, -1, 8, "YYYY-MM-DD"); //设置为去年同月份
        } else if (this.dateType == 8) {
          datatime = calcTime(this.selectTime, -1, 8, "YYYY-MM-DD"); //设置为去年
        }
      } else if (type == "环比") {
        if (this.dateType == 5) {
          datatime = calcTime(this.selectTime, -1, 5, "YYYY-MM-DD"); //设置为上个月
        } else if (this.dateType == 8) {
          datatime = calcTime(this.selectTime, -1, 8, "YYYY-MM-DD"); //设置为去年
        }
      }

      //设置起止日期
      let times = [];
      if (this.dateType == 5) {
        times = cycleTime(datatime, 5);
        this.startTime = times[0];
        this.endTime = times[1];
      } else if (this.dateType == 8) {
        times = cycleTime(datatime, 8);
        this.startTime = times[0];
        this.endTime = times[1];
      }
    },
    //获取能源成本数据
    getCostAnalysisData(params, type) {
      let me = this;

      let curParam = deepJSONCopy(params);
      curParam.title = type + "能源成本数据";
      curParam.startTime = this.startTime;
      curParam.endTime = this.endTime;
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: curParam
        })
        .then(res => {
          // if (res.data.status) {
          if (type == "本期") {
            if (res.data.status) {
              let bqCostData = res.data.records;

              //循环查找赋值能源名称给contentName
              if (bqCostData != null) {
                for (let i = 0; i < bqCostData.length; i++) {
                  for (let j = 0; j < me.energyTypeData.length; j++) {
                    if (bqCostData[i].contentId == me.energyTypeData[j].id) {
                      bqCostData[i].contentName = me.energyTypeData[j].name;
                      break;
                    }
                  }
                  if (bqCostData[i].name.indexOf("综合能耗") != -1) {
                    bqCostData[i].contentName = "本期";
                    me.costAnalysisData_temp.push(bqCostData[i]);
                  } else {
                    me.bq_costData.push(bqCostData[i]);
                  }
                }

                me.bqEgyCostPptData = reduceById(
                  me.bq_costData,
                  "contentId",
                  "dataValue",
                  "targetUnit",
                  me.energyDisplace
                );
                //占比分析
                me.scaleData = reduceById(
                  me.bq_costData,
                  "contentId",
                  "dataValue",
                  "targetUnit",
                  me.energyDisplace
                );

                //趋势分析
                me.egyCostTdcyData = me.bq_costData;
                getFixedDisplayData(me.egyCostTdcyData);
              } else {
                this.$message.warning("本期" + res.data.answerMsg);
              }
              me.getStartEndTime("同比");
              me.getCostAnalysisData(params, "同比");
            }
          } else if (type == "同比") {
            if (res.data.status) {
              let hbCostData = res.data.records;
              //循环查找赋值能源名称给contentName
              for (let i = 0; i < hbCostData.length; i++) {
                if (hbCostData[i].name.indexOf("综合能耗") != -1) {
                  hbCostData[i].contentName = "同比";
                  me.costAnalysisData_temp = me.costAnalysisData_temp.concat(
                    hbCostData[i]
                  );
                } else {
                  me.tb_costData.push(hbCostData[i]);
                }
              }
              me.tbEgyCostPptData = reduceById(
                me.tb_costData,
                "contentId",
                "dataValue",
                "targetUnit",
                me.energyDisplace
              );
            } else {
              this.$message.warning("同比" + res.data.answerMsg);
            }
            me.getStartEndTime("环比");
            me.getCostAnalysisData(params, "环比");
          } else if (type == "环比") {
            if (res.data.status) {
              let hbCostData = res.data.records;
              //循环查找赋值能源名称给contentName
              for (let i in hbCostData) {
                if (hbCostData[i].name.indexOf("综合能耗") != -1) {
                  hbCostData[i].contentName = "环比";
                  me.costAnalysisData_temp = me.costAnalysisData_temp.concat(
                    hbCostData[i]
                  );
                } else {
                  me.hb_costData.push(hbCostData[i]);
                }
              }
              getFixedDisplayData(me.costAnalysisData_temp);
              me.costAnalysisData = me.costAnalysisData_temp;
              me.hbEgyCostPptData = reduceById(
                me.hb_costData,
                "contentId",
                "dataValue",
                "targetUnit",
                me.energyDisplace
              );
            } else {
              this.$message.warning("环比" + res.data.answerMsg);
            }
            //循环计算得出总成本及同比环比比例等数据
            let name = "";
            let value = 0;
            let targetUnit = "";
            for (let i in me.bqEgyCostPptData) {
              value += me.bqEgyCostPptData[i].value;
            }
            me.bqEgyCostPptData.unshift({
              name: "总成本",
              value: value
            });
            value = 0;
            for (let i in me.tbEgyCostPptData) {
              value += me.tbEgyCostPptData[i].value;
            }
            me.tbEgyCostPptData.unshift({
              name: "总成本",
              value: value
            });
            value = 0;
            for (let i in me.hbEgyCostPptData) {
              value += me.hbEgyCostPptData[i].value;
            }
            me.hbEgyCostPptData.unshift({
              name: "总成本",
              value: value
            });
            //循环计算得出同比环比比例等数据
            for (let i in me.bqEgyCostPptData) {
              me.carbonData.push({
                name: me.bqEgyCostPptData[i].name,
                value: (me.bqEgyCostPptData[i].value / 10000).toFixed(2),
                unit: "万元",
                tbScale: "--",
                hbScale: "--",
                tbRatio: -1,
                hbRatio: -1
              });
              for (let j in me.tbEgyCostPptData) {
                if (
                  me.bqEgyCostPptData[i].name == me.tbEgyCostPptData[j].name
                ) {
                  me.carbonData[i].tbScale =
                    me.tbEgyCostPptData[j].value == 0
                      ? "--"
                      : (
                          (me.bqEgyCostPptData[i].value -
                            me.tbEgyCostPptData[j].value) /
                          me.tbEgyCostPptData[j].value *
                          100
                        ).toFixed(2);

                  me.carbonData[i].tbRatio =
                    me.tbEgyCostPptData[j].value == 0
                      ? "-1"
                      : me.bqEgyCostPptData[i].value >
                        me.tbEgyCostPptData[j].value
                        ? 1
                        : 0;
                }
              }
              for (let j in me.hbEgyCostPptData) {
                if (
                  me.bqEgyCostPptData[i].name == me.hbEgyCostPptData[j].name
                ) {
                  me.carbonData[i].hbScale =
                    me.hbEgyCostPptData[j].value == 0
                      ? "--"
                      : (
                          (me.bqEgyCostPptData[i].value -
                            me.hbEgyCostPptData[j].value) /
                          me.hbEgyCostPptData[j].value *
                          100
                        ).toFixed(2);

                  me.carbonData[i].hbRatio =
                    me.hbEgyCostPptData[j].value == 0
                      ? "-1"
                      : me.bqEgyCostPptData[i].value >
                        me.hbEgyCostPptData[j].value
                        ? 1
                        : 0;
                }
              }
            }
          }

          // }
        });
    }
  },
  mounted() {
    this.inintData();
  }
};
</script>

<style>
.coany-energy-block {
  float: left;
  width: 100%;
  height: 162px;
}

.coany-energy-block .el-icon-sort-up {
  color: #3fff3f;
}

.coany-energy-block .el-icon-sort-down {
  color: #ff2222;
}
.cost-any-value {
  font-size: 20px;
  font-weight: bold;
}
</style>




